.app {
  display: grid;
  grid-template-rows: 1fr 10fr;
  justify-items: center;
  max-width: 88.12rem;
  max-height: 63.12rem;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    180deg,
    rgba(43, 34, 21, 0.8) -1.22%,
    #22282e 101.33%
  );
  border-radius: 2rem;
}
